@font-face {
	font-family: "Allura"; // by TypeSETit
	src: url("/public/fonts/Allura-Regular.woff2") format("woff2");
	font-weight: normal;
	font-style: normal;
}

// Junicode 2
@font-face {
	font-family: "Junicode"; // by Peter S. Baker - https://github.com/psb1558/Junicode-font
	src: url("/public/fonts/Junicode-Light.woff2") format("woff2");
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: "Junicode";
	src: url("/public/fonts/Junicode-Bold.woff2") format("woff2");
	font-weight: bold;
	font-style: normal;
}

@font-face {
	font-family: "Junicode";
	src: url("/public/fonts/Junicode-LightItalic.woff2") format("woff2");
	font-weight: normal;
	font-style: italic;
}

@font-face {
	font-family: "Junicode";
	src: url("/public/fonts/Junicode-BoldItalic.woff2") format("woff2");
	font-weight: bold;
	font-style: italic;
}

:root {
	--heading-font: "Allura", cursive;
	--body-font: "Junicode", serif;
}

body {
	background-color: #603;
	background-position: center center;
	background-repeat: no-repeat;
	background-attachment: fixed;
	background-size: cover;
	background-image: url("/public/images/menu/carlos-lindner-sjBYA8dAw54-unsplash.jpg");
	color: #111;
	font-size: 62.5%;
	font-family: var(--body-font);
	padding: 0;

	@media (min-width: 920px) {
		padding: 0 20px;
	}
}

p {
	margin: 0 60px 1rem;
	font-size: 1.75em;
	text-align: center;
	line-height: 1.5;
}

p:last-child {
	margin-bottom: 0;
}

a {
	color: #222;
	font-weight: bold;
}

.container {
	width: auto;
	margin: 20px auto;
	background-color: rgba(255 255 239 / 90%);
	padding: 30px 10px;

	@media (min-width: 920px) {
		max-width: 920px;
		margin: 20px auto;
		padding: 30px 20px;
	}
}

h1 {
	font-family: var(--heading-font);
	font-size: 5em;
	margin-bottom: 0.4em;
	text-align: center;
}

.address {
	text-align: center;
	font-size: 1.75em;
}

section {
	margin: 2rem auto;
	max-width: 800px;
}

section + section,
footer {
	border-top: 1px solid rgba(100 0 0 / 25%);
	padding-top: 2rem;
}

h2 {
	font-family: var(--heading-font);
	font-size: 30px;
	margin-bottom: 12px;
	text-align: center;
}

h3 {
	font-family: var(--heading-font);
	font-size: 2.5em;
	margin: 10px 0;
	text-align: center;
}

li {
	width: 100%;
	font-size: 1.9em;
	margin: 5px 0 10px;

	.price {
		float: right;
	}
}

em {
	font-style: italic;
}
